@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100&display=swap);
body {
  margin: 0;
  background-color: #784475;
  font-family: 'JetBrains Mono', monospace;
}

h1, h2, h2, p {
  color: #262626;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

#animation {
  align-items: center;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-name: loading;
          animation-name: loading;
  background-color: #FFA149;
  border-radius: 15px;
  display: flex;
  height: 50px;
  justify-content: center;
  position: relative;
  text-align: center;
  width: 50px;
}

@-webkit-keyframes loading {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

@keyframes loading {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

.load p {
  color: black;
}

#header {
  align-items: center;
  background-color: #FFA149;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

#header a {
  color: #262626;
  text-decoration: none;
}

nav {
  align-items: center;
  display: flex;
}

nav a {
  padding: 10px;
}

.musicas {
  display: flex;
  flex-direction: column;
}

.track {
  display: inline;
}

.content-sound {
  display: flex;
}

.content-sound audio {
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
}

.text {
  align-items: flex-end;
  display: flex;
  flex-direction: row;
  margin-left: auto;
  margin-right: auto;
}

.text p {
  margin-bottom: 5.3px;
  margin-right: 15px;
  width: 60%;
}

.content-sound img {
  margin-left: 5%;
  margin-top: 0;
  width: 150px;
}

.album {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.albumFav {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 35%;
  width: 30%;
}

.album img {
  height: 200px;
  width: 200px;
}

.album .title-text {
  color: rgb(75, 116, 116);
  display: flex;
  flex-direction: column;
  font-size: 30px;
  padding: 5px;
  text-align: center;
}

.album .title-text p {
  margin-top: 0;
}

.album .title-text h2 {
  margin-bottom: 5px;
}

.soundAlbum {
  display: flex;
  flex-direction: column;
}

.login {
  display: flex;
}

.login img {
  margin: 60px;
  margin-top: 6%;
  height: 40%;
  width: 40%;
}

#cardLogin {
  align-items: center;
  background-color: #E1E1E1;
  border-radius: 3%;
  display: flex;
  flex-direction: column;
  height: 80vh;
  justify-content: center;
  margin: 60px;
  margin-left: 15%;
  width: 30%;
}

.login input {
  border-radius: 3%;
  margin-top: 10%;
  border: none;
  height: 50px;
  text-align: center;
  width: 210px;
}

.login button:disabled {
  background-color: #FFA149;
  border: none;
  box-shadow: none;
  height: 50px;
  margin-top: 3%;
  width: 215px;
}

.login button {
  background-color: #FFA149;
  border: none;
  box-shadow: 0px 2px 0px 2px #784475;
  height: 50px;
  width: 215px;
}

.user-info {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.user-info p, h3 {
  margin: 5px;
  margin-left: 20px;
  margin-top: 10px;
}

.informaçoes {
  margin-top: 10px;
}

.profile .link {
  background-color: rgb(75, 116, 116);
  border: solid 1px black;
  border-radius: 3px;
  box-shadow: 5px 5px 10px rgb(167, 202, 202);
  color: white;
  height: 60px;
  margin-top: 10px;
  padding: 10px;
  text-decoration: none;
}

.user-info img {
  background-color: rgb(75, 116, 116);
  border-radius: 3px;
  color: white;
  display: inline;
  margin-left: 20px;
  width: 300px;
}

.form-edit {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  width: 50%;
}

.form-edit input {
  width: 70%;
}

.form-edit textarea, button {
  margin-bottom: 15px;
  margin-top: 10px;
  width: 72%;
}

.form-edit button {
  background-color: rgb(75, 116, 116);
  border: solid 1px black;
  border-radius: 3px;
  box-shadow: 5px 5px 10px rgb(167, 202, 202);
  color: white;
  height: 30px;
  margin-top: 20px;
}

#search{
  display: flex;
  flex-direction: column;
}

#results {
  display: flex;
  align-items: center;
  justify-content: center;
}

#inputArtist {
  background-color: #E1E1E1;
  border: none;
  height: 35px;
  margin-right: 10px;
  text-align: center;
  width: 670px;
}

#results button {
  height: 37px;
  border: none;
  color: #262626;
  width: 300px;
  background-color: #FFA149;;
  margin-top: 14px;
}

.resume {
  color: #FFA149;
  font-size: 18px;
  margin-top: 5px;
  text-align: center;
}

#renderAllAbuns {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-left: 10%;
  margin-right: 10%;
}

.allAbuns{
  align-items: center;
  display: flex;
  background-color: #E1E1E1;
  border-radius: 5%;
  flex-direction: column;
  height: 350px;
  padding-bottom: 15px;
  margin: 10px;
  width: 220px;
}

.allAbuns img {
  font-size: 15px;
  padding: 25px;
  width: 170px;
  height: 170px;
}

.allAbuns p {
  font-size: 16px;
  text-decoration: none;
  margin-left: 10%;
  margin-right: 10%;
}

.allAbuns a {
  text-decoration: none;
}


